import React, {useState, useEffect, useRef} from 'react';
import Rating from 'react-rating';

const Errand = ({todo, todos, setTodos, num, stars, setCompletedNum}) => {

  const [editing, setEditing] = useState(false);
  const [newText, setNewText] = useState('');

  // è¨ˆç®—å·²å®Œæˆ errand çš„æ•¸é‡
  useEffect(() => {
    const completedTodos = todos.filter(item => {
      return item.completed === true;
    })
    setCompletedNum(completedTodos.length);
  }, [todos, setCompletedNum])

  // æ¨™è¨» errand å·²å®Œæˆ
  const doneHandler = () => {
    setTodos(todos.map(item => {
      if(item.id === todo.id) {
        return {
          ...item, completed: !item.completed
        }
      }
      return item
    }))
  }

  // åˆªé™¤ï¼šerrand
  const removeHandler = () => {
    setTodos(todos.filter(item => item.id !== todo.id))
  }

  // ç·¨è¼¯ï¼šerrand ç‹€æ…‹
  const editHandler = () => {
    setEditing(!editing);
  }

  // ç·¨è¼¯ï¼šerrand å…§å®¹
  const newInputHandler = (e) => {
    setNewText(e.target.value);
  }

  // ç·¨è¼¯ï¼šerrand å…§å®¹ç¢ºå®šç™¼é€
  const confirmHandler = (e) => {
    setTodos(
      todos.map((item) => {
        if(item.id === todo.id) {
          return {
            ...item, text: newText
          }
        }
        return item
      })
    )
    setEditing(false);
  }

  // ç·¨è¼¯ï¼šerrand å–æ¶ˆç·¨è¼¯
  const cancleHandler = () => {
    setEditing(false);
  }

  // ç·¨è¼¯ï¼šæ˜Ÿæ˜Ÿæ•¸é‡
  const starHandler = (value) => {
    setTodos(
      todos.map((item) => {
        if(item.id === todo.id) {
          return {
            ...item, stars: value
          }
        }
        return item
      })
    )
  }

  // ç•¶æ˜¯æ­£åœ¨ç·¨è¼¯ä¸­çš„ç‹€æ…‹æ™‚ï¼Œä¿ƒç™¼æŒ‰ä¸‹ input tag è®“ input æ˜¯é–ƒçˆé¡¯ç¤º
  const inputRef = useRef();

  useEffect(() => {
    const clickHandler = () => {
      inputRef.current.focus()
    }
    if(editing){
      clickHandler()
    }
  }, [editing])

  return(
    <div className="errand__container">
      {editing
      ? // åœ¨ç·¨è¼¯ä¸­
        
        <form className="errand__editing" onSubmit={confirmHandler}>
          <p className={todo.completed? "errand__completed--num errand__num" : "errand__num"}>{num + 1}</p>
          <div className="errand__editing-input">
            <input ref={inputRef} type="text" value={newText} placeholder="è¼¸å…¥ä¿®æ”¹äº‹é …" onChange={newInputHandler}></input>
          </div>
          <div className="errand__editing-btns">
            <i onClick={cancleHandler} className="fas fa-undo"></i>
            <i onClick={confirmHandler} type="submit" className="far fa-check-square"></i>
          </div>
        </form>

      :  // æ²’æœ‰åœ¨ç·¨è¼¯
      
        <div className="errand__notediting">
          <p className={todo.completed? "errand__completed--num errand__num" : "errand__num"}>{num + 1}</p>
          <div className="errand__stars">
          <Rating
              emptySymbol={<i className="far fa-star"></i>}
              fullSymbol={<i className="fas fa-star"></i>}
              quiet={true}
              stop={3}
              initialRating={stars}
              onClick={starHandler}/>
          </div>
          <div className="errand__notediting-content" onClick={doneHandler}>
            <p className={todo.completed? "errand__completed--text" : ""}>{todo.text}</p>
          </div>
          <div className="errand__notediting-btns">
            <div onClick={editHandler}>
              <i className="far fa-edit"></i>
            </div>
            <div onClick={removeHandler}>
              <i className="far fa-trash-alt"></i>
            </div>
          </div>
      </div>
      }
    </div>
  )
}

export default Errand;